@import url(https://fonts.googleapis.com/css2?family=EB+Garamond:wght@500;600;700&family=Roboto:wght@300;400;500;700&display=swap);
* {
    margin: 0;
    padding: 0;
    outline: none;
}

:root {
    --white: #ffffff;
    --black: #000000;
    --light: #E3E7E7;
    --dark: #292E4B;
    --prox: #778978;
    --eb: 'EB Garamond', serif;
}

a {
    text-decoration: none;
}

p {
    margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: 'EB Garamond', serif;
    font-family: var(--eb);
    margin: 0;
}

ol,
ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

button,
input {
    border: none;
    background-color: transparent;
}

.cbtn {
    background: linear-gradient(to right, #292E4B 50%, transparent 50%) no-repeat scroll right bottom / 210% 110% transparent !important;
    background: linear-gradient(to right, var(--dark) 50%, transparent 50%) no-repeat scroll right bottom / 210% 110% transparent !important;
    transition: ease-in .3s;
    -webkit-transition: ease-in .3s;
    -moz-transition: ease-in .3s;
    -ms-transition: ease-in .3s;
    -o-transition: ease-in .3s;
}

.cbtn:hover {
    background-position: left bottom !important;
    color: #ffffff;
    color: var(--white);
}

.cbn {
    background: linear-gradient(to right, #724706 50%, transparent 50%) no-repeat scroll right bottom / 210% 110% transparent !important;
}


/* ======Common css end======== */

.top_bar {
    background-color: #ffffff;
    background-color: var(--white);
    height: 35px;
}

.info p {
    font-size: 14px;
    margin-right: 15px;
    line-height: 35px;
}

.info p span {
    margin-left: 5px;
}

.logsign a {
    margin-left: 15px;
    line-height: 35px;
    text-transform: capitalize;
    padding: 6px 30px;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    font-family: 'EB Garamond', serif;
    font-family: var(--eb);
}

.act_log {
    color: violet;
}

.nav_active {
    color: rgb(186, 250, 11);
}


/* topbar end */

.navbar_nav {
    background-color: #292E4B;
    background-color: var(--dark);
}

.menu_btn li a {
    color: #ffffff;
    color: var(--white);
    position: relative;
    overflow: hidden;
    font-family: 'EB Garamond', serif;
    font-family: var(--eb);
}

.menu_btn li a::after {
    position: absolute;
    content: '';
    width: 100%;
    height: 2px;
    left: 0;
    top: 0px;
    transform: translateY(0);
    background-color: #778978;
    background-color: var(--prox);
    -webkit-transform: translateY(0);
    -moz-transform: translateY(0);
    -ms-transform: translateY(0);
    -o-transform: translateY(0);
    transition: ease-in .3s;
    -webkit-transition: ease-in .3s;
    -moz-transition: ease-in .3s;
    -ms-transition: ease-in .3s;
    -o-transition: ease-in .3s;
    visibility: hidden;
    opacity: .5;
}

.menu_btn li a:hover {
    color: #E3E7E7;
    color: var(--light);
}

.menu_btn li a:hover:after {
    top: 100%;
    transform: translateY(100%);
    -webkit-transform: translateY(100%);
    -moz-transform: translateY(100%);
    -ms-transform: translateY(100%);
    -o-transform: translateY(100%);
    visibility: visible;
    opacity: 1;
}


/* Navbar end */

#banner_part {
    background-color: #E3E7E7;
    background-color: var(--light);
}

.banner_text h1 {
    font-size: 56px;
    text-transform: capitalize;
    font-weight: 400;
    margin-bottom: 40px;
    color: #292E4B;
    color: var(--dark);
}

.banner_text p {
    color: #292E4B;
    color: var(--dark);
    font-size: 20px;
    line-height: 140%;
    max-width: 422px;
}

.banner_text button {
    width: 200px;
    height: 60px;
    margin-top: 20px;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    border: 2px solid #292E4B;
    border: 2px solid var(--dark);
    font-family: 'EB Garamond', serif;
    font-family: var(--eb);
    font-size: 22px;
    text-transform: capitalize;
}


/* banner part end */

#campaign_part {
    margin: 100px 0;
}

#campaign_part .cmp {
    position: relative;
}

#campaign_part .cmp button {
    position: absolute;
    left: 50%;
    bottom: 20px;
    transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -o-transform: translateX(-50%);
    background-color: #E3E7E7;
    background-color: var(--light);
    width: 120px;
    height: 40px;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}


/* campaign part end */

.heading {
    background-color: #292E4B;
    background-color: var(--dark);
    padding: 10px 0;
}

.heading h2 {
    text-transform: uppercase;
    color: #ffffff;
    color: var(--white);
    font-weight: 500;
    font-size: 34px;
}

.heading p {
    color: #ffffff;
    color: var(--white);
    text-transform: capitalize;
    letter-spacing: 1.2px;
}


/* section head end */

#product_part {
    background-color: #E3E7E7;
    background-color: var(--light);
    padding-bottom: 50px;
}

.product_img {
    position: relative;
    overflow-x: hidden;
}

.product_img .q_view {
    color: #292E4B;
    color: var(--dark);
    position: absolute;
    top: 37%;
    left: 0%;
    width: 200px;
    height: 50px;
    line-height: 50px;
    transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -o-transform: translateX(-50%);
    text-transform: capitalize;
    visibility: hidden;
    opacity: 0;
    background-color: #E3E7E7;
    background-color: var(--light);
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    transition: ease-in .4s;
    -webkit-transition: ease-in .4s;
    -moz-transition: ease-in .4s;
    -ms-transition: ease-in .4s;
    -o-transition: ease-in .4s;
}

.product_img:hover .q_view {
    visibility: visible;
    opacity: 1;
    left: 50%;
}

.product_img .n_shop {
    color: #292E4B;
    color: var(--dark);
    position: absolute;
    bottom: 37%;
    right: 0%;
    width: 200px;
    height: 50px;
    line-height: 50px;
    transform: translateX(50%);
    -webkit-transform: translateX(50%);
    -moz-transform: translateX(50%);
    -ms-transform: translateX(50%);
    -o-transform: translateX(50%);
    text-transform: capitalize;
    visibility: hidden;
    opacity: 0;
    background-color: #E3E7E7;
    background-color: var(--light);
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    transition: ease-in .4s;
    -webkit-transition: ease-in .4s;
    -moz-transition: ease-in .4s;
    -ms-transition: ease-in .4s;
    -o-transition: ease-in .4s;
}

.product_img:hover .n_shop {
    visibility: visible;
    opacity: 1;
    right: 50%;
}

.pro_text {
    margin-bottom: 20px;
}

.pro_text h5 {
    color: #000000;
    color: var(--black);
}

.pro_text p {
    font-family: 'EB Garamond', serif;
    font-family: var(--eb);
    font-weight: 500;
}

.load_btn {
    padding: 7px 60px;
    border-radius: 7px;
    -webkit-border-radius: 7px;
    -moz-border-radius: 7px;
    -ms-border-radius: 7px;
    -o-border-radius: 7px;
    border: 2px solid #292E4B;
    border: 2px solid var(--dark);
}


/* product part end */

#collection_part {
    padding: 50px 0;
    background-color: #C4C4C4;
}


/* collection part end */

#Blog_part {
    background-color: #E3E7E7;
    background-color: var(--light);
    padding: 40px 0;
}

.blog_text h3 {
    font-size: 26px;
    padding-top: 10px;
}

.blog_text h4 {
    font-size: 22px;
    color: #778978;
    color: var(--prox);
}

.blog_text p {
    color: #555;
}


/* blog part end */

.free_shiping {
    background-color: #292E4B;
    background-color: var(--dark);
    padding: 60px 0;
}

.icn {
    margin-right: 15px;
}

.icn .icn_stl {
    font-size: 40px;
    color: #ffffff;
    color: var(--white);
}

.Ebox h3 {
    color: #ffffff;
    color: var(--white);
    font-size: 22px;
    font-weight: 500;
    text-transform: capitalize;
    letter-spacing: 3.1px;
}

.Ebox p {
    color: #E3E7E7;
    color: var(--light);
}


/* Shiping part end */

#footer_part {
    background-color: #E3E7E7;
    background-color: var(--light);
}

#footer_part h4 {
    font-size: 22px;
    font-weight: 700;
    margin-bottom: 10px;
}

.foot_contain {
    padding: 50px 0;
    color: #292E4B;
    color: var(--dark);
}

.f_logo {
    width: 70px;
}

.logo_col {
    padding: 0 30px;
}

.logo_col p {
    margin-top: 10px;
}

.foot_nav li {
    margin-bottom: 7px;
    cursor: pointer;
}

.foot_contact li {
    margin-bottom: 10px;
}

.foot_contact li p {
    margin-left: 10px;
}

.social_icn {
    margin-top: 40px;
    width: 130px;
}

.social_icn .sIcon {
    font-size: 24px;
}

.footer_form form {
    max-width: 80%;
    background-color: #292E4B;
    background-color: var(--dark);
    height: 44px;
    padding-left: 30px;
    padding-right: 6px;
    border-radius: 20px;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    -ms-border-radius: 20px;
    -o-border-radius: 20px;
    margin-top: 20px;
}

.footer_form form input {
    color: #ffffff;
    color: var(--white);
    width: 70%;
}

.footer_form form button {
    background-color: #E3E7E7;
    background-color: var(--light);
    height: 70%;
    width: 120px;
    border-radius: 14px;
    -webkit-border-radius: 14px;
    -moz-border-radius: 14px;
    -ms-border-radius: 14px;
    -o-border-radius: 14px;
}

.crCard {
    padding-top: 40px;
    max-width: 140px;
}

.crCard .crd_icn {
    font-size: 22px;
}

.bottom {
    background-color: #fff;
}


/* Footer part end */

.banner_pic {
    position: relative;
}

.reg_padding {
    padding: 60px 0;
}

.sign_form {
    position: absolute;
    width: 400px;
    height: 550px;
    left: 105px;
    top: 35px;
    background-color: rgba(41, 46, 75, 0.7);
    padding: 45px 60px;
}

.sign_form h3 {
    color: #ffffff;
    color: var(--white);
    margin-bottom: 26px;
    position: relative;
}

.sign_form h3::after {
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #ffffff;
    background-color: var(--white);
    left: 0;
    bottom: -8px;
    content: '';
}

.sign_form form label {
    color: #ffffff;
}

.sign_form form .form-control {
    line-height: .8;
    background-color: #292E4B;
    background-color: var(--dark);
    border: none;
    color: #ffffff;
    color: var(--white);
}

.sign_form form .form-text {
    color: #ffffff;
    color: var(--white);
    text-transform: capitalize;
}

.usersName .form-control {
    width: 48%;
}
